@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../fonts/stylesheet.css";


* {
    box-sizing: border-box; }

body {
    font-size: 16px;
    line-height: 19px;
    font-family: 'Proxima Nova Regular'; }

p {
    margin: 0; }

a {
    color: #030303;
    transition: .3s;
    &:hover {
        transition: .3s;
        color: #FF6BA5;
        text-decoration: none; } }


.container {
    max-width: 1200px;
    width: 100%;
    margin: auto;
    padding: 0 15px; }

ul, li {
    padding: 0;
    margin: 0; }
ul {
    list-style: none; }

.dotted-btn {
    border: 2px dashed #FA87AE;
    border-radius: 80px;
    padding: 30px 50px;
    background-color: transparent;
    position: relative;
    cursor: pointer;
    font-size: 15px;
    line-height: 0px;
    color: #F84388;
    font-family: 'Proxima Nova Bold';
    &:after {
        content: '';
        position: absolute;
        border: 1px solid #F8CFDF;
        width: calc(100% + 14px);
        height: calc(100% + 14px);
        box-sizing: border-box;
        border-radius: 80px;
        top: -7px;
        left: -7px; } }

.fill-btn {
    border: none;
    border-radius: 80px;
    padding: 40px 50px;
    background: #FD67A6;
    box-shadow: inset 9px -8px 20px #F9407E;
    position: relative;
    cursor: pointer;
    font-size: 15px;
    line-height: 0px;
    color: #fff;
    font-family: 'Proxima Nova Bold';
    box-shadow: 15px -6px 30px rgba(248, 67, 136, 0.25), inset 9px -8px 20px #F9407E;
    min-width: 270px;
    &:after {
        content: '';
        position: absolute;
        border: 1px solid #F8CFDF;
        width: calc(100% + 12px);
        height: calc(100% + 12px);
        box-sizing: border-box;
        border-radius: 80px;
        top: -6px;
        left: -6px; } }

.dotted-txt {
    font-size: 18px;
    line-height: 0;
    margin-top: 28px;
    color: #FA4288;
    padding-bottom: 15px;
    border-bottom: 1px dashed #FA4288;
    cursor: pointer; }

.lines {
    position: absolute;
    z-index: 0; }

.petals {
    position: absolute;
    z-index: 0; }

.section-title {
    text-align: center;
    font-family: 'Berlingske Serif SemiBold';
    font-size: 30px;
    line-height: 36px;
    color: #272727;
    margin-bottom: 10px;
    span {
        font-style: italic;
        color: #FB5793;
        line-height: 42px; } }

.section-post-title {
    font-size: 18px;
    line-height: 141.52%;
    text-align: center;
    color: #747474; }

.service-lines {
    position: absolute;
    z-index: 5; }

.service-bg {
    position: absolute;
    z-index: 10;
    left: 60px;
    top: 85px; }

.service-img {
    position: absolute;
    z-index: 15; }



@import "includes/header.sass";
@import "includes/services.sass";
