@font-face {
	font-family: 'Proxima Nova Regular';
	src: url('ProximaNova-Regular.eot');
	src: local('Proxima Nova Regular'), local('ProximaNova-Regular'),
		url('ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
		url('ProximaNova-Regular.woff') format('woff'),
		url('ProximaNova-Regular.ttf') format('truetype');
}

@font-face {
	font-family: 'Proxima Nova Bold';
	src: url('ProximaNova-Bold.eot');
	src: local('Proxima Nova Bold'), local('ProximaNova-Bold'),
		url('ProximaNova-Bold.eot?#iefix') format('embedded-opentype'),
		url('ProximaNova-Bold.woff') format('woff'),
		url('ProximaNova-Bold.ttf') format('truetype');
}

@font-face {
	font-family: 'Proxima Nova Bold';
	src: url('ProximaNova-BoldIt.eot');
	src: local('Proxima Nova BoldIt'), local('ProximaNova-BoldIt'),
		url('ProximaNova-BoldIt.eot?#iefix') format('embedded-opentype'),
		url('ProximaNova-BoldIt.woff') format('woff'),
		url('ProximaNova-BoldIt.ttf') format('truetype');
	font-style: italic;
}

@font-face {
	font-family: 'Proxima Nova Regular';
	src: url('ProximaNova-RegularIt.eot');
	src: local('Proxima Nova RegularIt'), local('ProximaNova-RegularIt'),
		url('ProximaNova-RegularIt.eot?#iefix') format('embedded-opentype'),
		url('ProximaNova-RegularIt.woff') format('woff'),
		url('ProximaNova-RegularIt.ttf') format('truetype');
	font-style: italic;
}

@font-face {
	font-family: 'Berlingske Serif Regular';
	src: url('BerlingskeSerif-Regular.ttf');
}

@font-face {
	font-family: 'Berlingske Serif SemiBold';
	src: url('BerlingskeSerif-DBd.ttf');
	font-style: normal;
}

@font-face {
	font-family: 'Berlingske Serif SemiBold';
	src: url('BerlingskeSerif-DBdItalic.ttf');
	font-style: italic;
}