#services {
    padding-top: 30px;
    position: relative; }

.section-post-title {
    margin-top: 20px; }

.tabs {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    max-width: 970px;
    width: 100%;
    margin: auto;
    margin-bottom: 100px; }

.tab {
    background: #F9F9F9;
    border-radius: 80px;
    border: none;
    line-height: 0;
    padding: 30px 45px;
    margin-top: 40px;
    font-size: 16px;
    color: #D5D5D5;
    font-family: 'Proxima Nova Bold';
    &:focus {
        outline: 0; } }


.active-tab {
    background: #FFF2F9;
    color: #F84388;
    position: relative;
    &:after {
        content: '';
        position: absolute;
        border: 1px dashed #F84388;
        border-radius: 80px;
        width: calc(100% - 8px);
        height: calc(100% - 8px);
        top: 4px;
        left: 4px; } }

.service {
    display: flex;
    margin-bottom: 130px; }


.service-content {
    position: relative;
    width: 50%;
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.017em;
    color: #747474;
    z-index: 30; }

.service-img-container {
    width: 50%;
    padding: 32px;
    position: relative; }

.service-title {
    font-size: 20px;
    line-height: 24px;
    color: #272727;
    font-family: 'Berlingske Serif SemiBold';
    span {
        color: #FB5793;
        line-height: 38px;
        font-style: italic; } }

.service-desc {
    margin: 35px 0 0; }

.price-list {
    margin: 35px 0 45px;
    span {
        color: #FA5290; } }

.price-lst-itm {
    padding-left: 13px;
    position: relative;
    &:before {
        content: '';
        position: absolute;
        width: 5px;
        height: 5px;
        background: #FA5290;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%; } }

.ptl1 {
    top: 275px;
    left: 0; }

.svl1 {
    left: 25px;
    top: 215px; }

.svimg1 {
    top: 15px;
    left: 35px; }

.ptl2 {
    top: -30px;
    left: 0; }

.svl2 {
    left: 20px;
    top: 350px; }

.svimg2 {
    top: 30px;
    left: 30px; }

.service-wrap {
    padding-left: 20px; }

.price-lst-title {
    margin-bottom: 10px;
    margin-top: 7px;
    font-size: 16px;
    line-height: 177.8%;
    letter-spacing: 0.017em;
    color: #747474;
    font-family: 'Proxima Nova Bold'; }

.price-lst-title + .price-list {
    margin: 0 0 13px; }

.price-list {
    font-style: italic; }
