#header {
    background-image: url('../img/header-bg.svg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    padding-top: 20px;
    margin-top: 5px; }

.header-top {
    display: flex;
    justify-content: space-between;
    align-items: flex-end; }

.menu-container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    margin: 0 80px;
    justify-content: center;
    align-self: flex-start; }

.contacts {
    display: flex;
    justify-content: space-between;
    margin-top: 45px;
    margin-bottom: 40px; }

.header-phone {
    color: #A1A1A1;
    font-family: 'Berlingske Serif SemiBold'; }

.adres {
    font-size: 14px;
    line-height: 17px;
    color: #B3B3B3; }

.header-messangers {
    display: flex;
    justify-content: space-between;
    img {
        margin-right: 9px; }
    .messangers-phone {
        font-size: 14px;
        line-height: 17px;
        color: #FF6AA4;
        font-family: 'Proxima Nova Bold'; } }


.menu {
    display: flex;
    justify-content: space-between; }

.fbk-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end; }



.header-content {
    margin-top: 190px;
    position: relative;
    display: flex;
    justify-content: flex-end; }

.hdr-img-container {
    position: absolute;
    left: -240px;
    top: -110px;
    max-width: 770px;
    width: 100%;
    height: 745px;
    overflow: hidden;
    border-radius: 50%;
    z-index: 10; }

.master-img {
    position: absolute;
    top: -9999px;
    bottom: -9999px;
    left: -9999px;
    right: -9999px;
    margin: auto; }


.hdr-text-container {
    max-width: 570px;
    width: 100%;
    z-index: 10; }



.header-title {
    font-size: 28px;
    color: #F84388;
    line-height: 0;
    margin-bottom: 60px;
    font-family: 'Berlingske Serif SemiBold';
    font-style: italic; }

.master-name {
    font-size: 72px;
    line-height: 0px;
    color: #1B1B1B;
    margin-bottom: 60px;
    font-family: 'Berlingske Serif SemiBold'; }

.header-text {
    font-size: 16px;
    line-height: 28px;
    letter-spacing: 0.017em;
    color: #747474;
    margin-bottom: 30px; }

.hdr-buttons {
    margin: 70px 0 135px;
    display: flex;
    justify-content: space-between;
    .fill-btn {
        box-shadow: none; } }

.play-container {
    position: absolute;
    top: 130px;
    left: 260px;
    z-index: 5; }

.hdr-play-txt {
    position: absolute;
    bottom: 60px;
    left: 140px;
    font-size: 14px;
    line-height: 17px;
    color: #F84388;
    padding-bottom: 4px;
    cursor: pointer;
    border-bottom: 1px solid #F84388;
    font-family: 'Proxima Nova Bold';
    font-style: italic; }

.l1 {
    top: -80px;
    left: -135px; }

.l2 {
    bottom: -100px;
    left: -215px; }
